.portfolio-footer {
    height: 3rem;
    background-color: #333;
    display: flex;
    align-items: center;
    color: white
}

.footer-content {
    width: 100%;
    display: flex;
    padding: 0 0.75rem;
    align-items: center;
    gap: 1rem;
    justify-content: center;
}

.footer-content a {
    align-items: center;
    text-align: center;
    width: 1.5rem;
    height: 1.5rem;
    border-radius: 10%;
    overflow: hidden;
}

.dark-icon {
    border: 0.1rem white solid;
    box-shadow: inset 0 0 0 40px white, inset 0 0 0 80px #fff;
}

.footer-logo {
    width: 100%;
    height: 100%;
}